* {
  font-family: "Allerta Stencil", sans-serif;
  font-weight: 400;
  font-style: normal;
  margin: 0;
  padding: 0;
}

.body {
  width: 100%;
  height: 100vh;
  background-color: #3a532c;
}

.body h1 {
  text-align: center;
  font-size: 50px;
  color: white;
}

.calculator {
  margin: 10px auto;
  width: 350px;
  height: 450px;
  border: none;
  border-radius: 15px;
  background-color: white;
  display: grid;
  grid-template-areas:
    "result-row result-row result-row result-row"
    "reset reset reset divide"
    "seven eight nine multiply-btn"
    "four five six minus"
    "one two three plus"
    "zero-btn zero-btn decimal-btn equal";
  gap: 1px;
  overflow: hidden;
  box-shadow: 0 1px 5px black;
}

.calculator * {
  padding: 0 auto;
  border: none;
  border-radius: 1px;
  font-size: 40px;
  background-color: #7dc814;
  color: white;
}

.result-row {
  grid-area: result-row;
  height: auto;
  margin: 0;
  padding: 0 20px;
  font-size: 45px;
  align-self: center;
  background-color: transparent;
  color: #3a532c;
}

.reset {
  grid-area: reset;
}

.decimal-btn {
  grid-area: decimal-btn;
}

.plus {
  grid-area: plus;
  background-color: #549e00;
}

.minus {
  grid-area: minus;
  background-color: #549e00;
}

.multiply-btn {
  grid-area: multiply-btn;
  background-color: #549e00;
}

.divide {
  grid-area: divide;
  background-color: #549e00;
}

.equal {
  grid-area: equal;
  background-color: #549e00;
}

.zero-btn {
  grid-area: zero-btn;
}

.one {
  grid-area: one;
}

.two {
  grid-area: two;
}

.three {
  grid-area: three;
}

.four {
  grid-area: four;
}

.five {
  grid-area: five;
}

.six {
  grid-area: six;
}

.seven {
  grid-area: seven;
}

.eight {
  grid-area: eight;
}

.nine {
  grid-area: nine;
}
